import Vue from 'vue';
import i18n from '../../../i18n';
import { erase } from '../../../../../prelude/array';
import { addTimespan } from '../../../../../prelude/time';
import { formatDateTimeString } from '../../../../../misc/format-time-string';
export default Vue.extend({
    i18n: i18n('common/views/components/poll-editor.vue'),
    data() {
        return {
            choices: ['', ''],
            multiple: false,
            expiration: 'infinite',
            atDate: formatDateTimeString(addTimespan(new Date(), 1, 'days'), 'yyyy-MM-dd'),
            atTime: '00:00',
            after: 0,
            unit: 'second'
        };
    },
    watch: {
        choices() {
            this.$emit('updated');
        }
    },
    methods: {
        onInput(i, e) {
            Vue.set(this.choices, i, e.target.value);
        },
        add() {
            this.choices.push('');
            this.$nextTick(() => {
                this.$refs.choices.childNodes[this.choices.length - 1].childNodes[0].focus();
            });
        },
        remove(i) {
            this.choices = this.choices.filter((_, _i) => _i != i);
        },
        destroy() {
            this.$emit('destroyed');
        },
        get() {
            const at = () => {
                return new Date(`${this.atDate} ${this.atTime}`).getTime();
            };
            const after = () => {
                let base = parseInt(this.after);
                switch (this.unit) {
                    case 'day': base *= 24;
                    case 'hour': base *= 60;
                    case 'minute': base *= 60;
                    case 'second': return base *= 1000;
                    default: return null;
                }
            };
            return Object.assign({ choices: erase('', this.choices), multiple: this.multiple }, (this.expiration === 'at' ? { expiresAt: at() } :
                this.expiration === 'after' ? { expiredAfter: after() } : {}));
        },
        set(data) {
            if (data.choices.length == 0)
                return;
            this.choices = data.choices;
            if (data.choices.length == 1)
                this.choices = this.choices.concat('');
            this.multiple = data.multiple;
            if (data.expiresAt) {
                this.expiration = 'at';
                this.atDate = this.atTime = data.expiresAt;
            }
            else if (typeof data.expiredAfter === 'number') {
                this.expiration = 'after';
                this.after = data.expiredAfter;
            }
            else {
                this.expiration = 'infinite';
            }
        }
    }
});
