const dateTimeIntervals = {
    'days': 86400000,
    'hours': 3600000,
};
export function DateUTC(time) {
    const r = new Date(0);
    r.setUTCFullYear(time[0], time[1], time[2]);
    if (time[3])
        r.setUTCHours(time[3], ...time.slice(4));
    return r;
}
export function isTimeSame(a, b) {
    return (a.getTime() - b.getTime()) === 0;
}
export function isTimeBefore(a, b) {
    return (a.getTime() - b.getTime()) < 0;
}
export function isTimeAfter(a, b) {
    return (a.getTime() - b.getTime()) > 0;
}
export function addTimespan(x, value, span) {
    return new Date(x.getTime() + (value * dateTimeIntervals[span]));
}
export function subtractTimespan(x, value, span) {
    return new Date(x.getTime() - (value * dateTimeIntervals[span]));
}
